@import url("https://fonts.googleapis.com/css2?family=Protest+Revolution&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400&display=swap");

.nav {
  background: rgb(174, 235, 238);
  background: linear-gradient(
    243deg,
    rgba(174, 235, 238, 1) 0%,
    rgba(92, 177, 101, 1) 100%
  );
  padding: 25px 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 50px;
}

.main-1 {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.main-1 li {
  margin-right: 20px;
}

.main-1 li:last-child {
  margin-right: 0;
}

.main-1 li a {
  text-decoration: none;
  color: #fff;
  font-family: "Rajdhani", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.menubar {
  display: none;
}

.menubar span {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 25px;
  height: 3px;
  background-color: #fff;
  margin-bottom: 5px;
}

.menubar span:nth-child(2) {
  width: 12.5px;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.text {
  color: white;
  font-family: "Rajdhani", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.text a {
  color: white;
  text-decoration: none;
}

.text a:hover {
  text-decoration: underline;
}

.text-2 {
  font-size: 35px;
  font-family: "Protest Revolution", sans-serif;
  font-weight: 400;
  font-style: normal;
}

@media screen and (max-width: 768px) {
  .main-1 {
    display: none;
  }
  .nav {
    gap: 25px;
    align-items: flex-start;
    flex-direction: row-reverse;
  }
  .show {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 9px;
    align-items: center;
    position: absolute;
    background: #000;
    width: 93%;
    gap: 21px;
    z-index: 1010;
    margin: 0 auto;
    top: 92px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
  }

  .menubar {
    display: block;
    cursor: pointer;
  }

  .main-2 {
    display: block;
    text-align: center;
    margin-bottom: 10px;
  }
}

::-webkit-scrollbar {
  width: 0;
}
